@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background-color: rgb(255, 255, 255)
  
}

* {
  margin: 0;
  padding: 0;
  
}
.glass{
  margin-left: 50px;
  margin-right: 50px;
  margin-top: 20px;
  margin-bottom: 20px;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 20px;
  border:1px solid rgba(255, 255, 255, 0.18);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
  @media only screen and (max-width: 600px) {
    margin-left: 10px;
    margin-right: 10px;
  }
}


.glass1{
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 20px;
  border:1px solid rgba(255, 255, 255, 0.18);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);

}


  /*---cookies---*/
  .wrappercookies{
    
    position: fixed;
    z-index: 50;
    bottom: 1%;
    left: 1%;
    width: 365px;
    background: #fff;
    padding: 25px 25px 25px 25px;
    border-radius: 15px;
    box-shadow: 1px 7px 14px -5px rgba(0,0,0,0.15);
    text-align: center;
    box-shadow: 0 -2px 16px rgba(32, 32, 32, 0.15);
  
    transition: 400ms;

  }
  
  .wrappercookies.hide{
    opacity: 0;
    pointer-events: none;
    transform: scale(0.8);
    transition: all 0.3s ease;
  }
  
  .wrappercookies img{
    max-width: 90px;
    object-position: center ;
    margin-left: auto;
    margin-right: auto;
  }
  .content header{
    font-size: 25px;
    font-weight: 600;
  }
  .content{
    margin-top: 10px;
  }
  .content p{
    color: #858585;
    margin: 5px 0 20px 0;
  }
  .content .buttons{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .buttons button{
    padding: 10px 20px;
    border: none;
    outline: none;
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    border-radius: 5px;
    background: #000000;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  .buttons button:hover{
    transform: scale(0.97);
  }
  .buttons .item{
    margin: 0 10px;
  }
  .buttons a{
    color: #000000;
  }
  
  @media only screen and (max-width: 600px) {
    .wrappercookies{
      width: 250px;
    }
    .content header{
      font-size: 15px;
      font-weight: 600;
    }
    .content p{
      font-size: small;
    }
    .content .buttons{
      font-size: small;
    }
    .buttons .item{
      font-size: 11px;
    }
  
    .buttons a{
      color: #000000;
      font-size: small;
    }
   
  }
  /*--cookies end--*/

  